.hero {
  display: flex;
  justify-content: space-between;
}

.left-h {
  padding: 2rem;
  padding-top: 1.5rem;
  flex: 3
}

.right-h {
  flex: 1;
  position: relative;
  background-color: var(--gold);
}

.the-best-ad{
  margin-top: 4rem;
  background-color: #363d42;
  border-radius: 4rem;
  width: fit-content;
  padding: 20px 13px;
  text-transform: uppercase;
  color: white;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.hero-text{
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  text-transform: uppercase;
  font-size: 4.5rem;
  font-weight: bold;
  color: white;
  text-overflow: inherit;
}


.hero-text>div:nth-of-type(3){
  font-size: 1rem;
  font-weight: 200;
  text-transform: none;
  letter-spacing: 1px;
  width: 80%;
}

.figures{
  display: flex;
  gap: 2rem;
  margin-top: 1rem;
}

.figures>div{
  display: flex;
  flex-direction: column;
}

.figures>div>span:nth-of-type(1){
  color: white;
  font-size: 2rem;
}

.figures>div>span:nth-of-type(2){
  color: var(--gray);
  text-transform: uppercase;
}

.hero-buttons{
  display: flex;
  gap: 1rem;
  font-weight: normal;
  margin-top: 1rem;
}

.hero-buttons>:nth-child(2){
  color: white;
  background-color: transparent;
  width: 8rem;
  border: 2px solid var(--gold);
}

.hero-buttons>:nth-child(1){
  color: white;
  background-color: var(--gold);
  width: 8rem;
}

.right-h>.btn{
  position: absolute;
  right: 3rem;
  top: 2rem;
  color: black;
}

.heart-rate{
  display: flex;
  flex-direction: column;
  gap: 1rem;
  background-color: var(--darkGrey);
  width: fit-content;
  padding: 1rem;
  align-items: flex-start;
  border-radius: 5px;
  position: absolute;
  right: 4rem;
  top: 7rem;
}

.heart-rate>:nth-child(2){
  color: var(--gray);
}

.heart-rate>:nth-child(3){
  color: white;
  font-size: 1.5rem;
}

.heart-rate>img{
  width: 2rem;
}

.hero-image{
  position: absolute;
  top: 8rem;
  right: 8rem;
  width: 26rem;
  height: 28rem;
  /* margin-bottom: 324rem; */
}

.hero-image-back{
  position: absolute;
  top: 4rem;
  right: 20rem;
  z-index: -1;
  width: 15rem;
}

.goals{
  display: flex;
  gap: 2rem;
  background-color: var(--caloryCard);
  border-radius: 5px;
  padding: 1rem;
  width: fit-content;
  position: absolute;
  top: 27rem;
  right: 35rem;
}

.goals>img{
  width: 3rem;
}

.goals>div{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.goals>div>:nth-child(1){
  color: var(--gray);
}

.goals>div>:nth-child(2){
  color: white;
  font-size: 1.5rem;
}

.hero-subtext {
  color: white;
  align-items: center;
}


@media screen and (max-width : 768px){
  .hero{
    flex-direction: column;
  }
  .hero-blur{
    width: 14rem;
  }


  .the-best-ad {  
    font-size: small;

    align-self: center;
    margin: 0 !important;
    font-size: small;
    transform: scale(0.8);
    margin : 0 0 0 15% !important;
  }
  .hero-text{
    font-size: xx-large;
    align-items: center;
    justify-content: center;
  }
  .hero-text > div:nth-of-type(3){
    font-size: small;
    font-weight: 200;
    letter-spacing: 1px;
    text-align: center;
  }
  .hero-buttons{
    justify-content: center;
  }
  .figures > div > span:nth-of-type(1){
    font-size: large;
  }
  .figures > div >span:nth-of-type(2){
    font-size: small;
  }
  .right-h{
    position: relative;
    background: none;
  }
  .heart-rate{
    left: 1rem;
    top: 2rem;
  }
  .goals{
    position: relative;
    top: 5rem;
    left: 2rem;

  }
  .goals>img{
    width: 2rem;
  }
  .goals > div > :nth-child(2){
    color: white;
    font-size: 1rem;
  }
  .hero-image{
    position: relative;
    width: 15rem;
    left: 7rem;
    top: 4rem;
    align-self: center;
  }
  .hero-image-back{
    width: 15rem;
    left: 2rem;
    top: 0rem;
  }
  

}

.hero-blur {
  width: 22rem;
  height: 30rem;
  left: 0;
}